import React, { ChangeEvent } from "react";
import {  defaultOptions } from '../options';
import { DbService } from '../misc/dbService';
import { IConfig } from "m-formatter";
import { FaQuestionCircle, FaSmileWink } from "react-icons/fa";
import "./FormatterOptions.sass"
import { StateService } from '../misc/stateService';

interface IState {
  indentLength: string;
  lineWidth: string;
  inlineCss: string;
  alignExpressionsByEqual: boolean;
  numBrackets: string;
  indentLengthErr: string,
  numBracketsErr: string,
  inlineCssErr: string,
  inlineCssToggle: boolean;
  customCss: boolean; 
  includeComments: boolean;
}

export class FormatterOptions extends React.Component<{initializer: (comp: FormatterOptions) => any}, IState>
{
  constructor(props)
  {
    super(props);
    this.state = this.configToState({...defaultOptions});
    this.props.initializer(this);
  }
  
  configToState(options: IConfig)
  {
    return {
      indentLength: options.indentationLength.toString(),
      lineWidth: options.lineWidth.toString(),
      inlineCss: options.inlineCss.toString(),
      alignExpressionsByEqual: options.alignPairedExpressionsByEqual,
      numBrackets: options.numBrackets.toString(),
      inlineCssToggle: options.inlineCssToggle,
      includeComments: options.includeComments,
      indentLengthErr: null,
      numBracketsErr: null,
      customCss: false,
      inlineCssErr: null,
    }
  }
  
  resetOptions = () => {
    this.setState(this.configToState({...defaultOptions}));
  }

  handleChange = async (ev: ChangeEvent<any>) => {
    let d = {};
    if(ev.target.type == "checkbox")
      d[ev.target.name] = ev.target.checked;
    else
      d[ev.target.name] = ev.target.value;
    this.setState(d);
  }
  
  async componentWillUnmount()
  {
    await DbService.store("formatterOptions", this.state);
  }
  
  async componentDidMount()
  {
    let state = await DbService.get<IState>("formatterOptions");
    if(state)
      this.setState(state);  
      
    window.addEventListener("beforeunload", this.onBeforeUnload)
  }
  
  onBeforeUnload = async () => {
    await DbService.store("formatterOptions", this.state);
  }

  getConfig(): IConfig
  {
    this.setState({
      numBracketsErr: null,
      indentLengthErr: null,
      inlineCssErr: null
    });
    function checkValidNumber(num: number)
    {
      return num != null && isNaN(num) == false;
    }
    let success = true;
    let state = {} as any;

    let indentLength = +this.state.indentLength;
    if(checkValidNumber(indentLength) == false)
    {
      success = false;
      state.indentLengthErr = "Could not parse integer.";
    }
    else if(indentLength < 0)
    {
      success = false;
      state.indentLengthErr = "Invalid value, must be >= 0"
    }

    let lineWidth = Number.parseInt(this.state.lineWidth);

    let numBrackets = +this.state.numBrackets;
    if(checkValidNumber(numBrackets) == false)
    {
      success = false;
      state.numBracketsErr = "Could not parse integer.";
    }
    else if(numBrackets < 0)
    {
      success = false;
      state.numBracketsErr = "Invalid value, must be >= 0";
    }
    
    
    let css;
    if(this.state.inlineCssToggle == false)
      css = null;
    else if(this.state.customCss == true)
      css = this.state.inlineCss;
    
    this.setState(state);
    if(success == false)
      throw new Error("Invalid config");

    return  {
      ...defaultOptions,
      indentationLength: indentLength,
      lineWidth: lineWidth,
      numBrackets: numBrackets,
      inlineCss: this.state.inlineCssToggle == true ? this.state.inlineCss : null,
      alignPairedExpressionsByEqual: this.state.alignExpressionsByEqual,
      indentation: defaultOptions.ws.repeat(indentLength),
      includeComments: this.state.includeComments
    };
  }
  
  render()
  {
    return (
      <div className="options-container">
        <div>
          <span className="hint">I will store your preferred options in your browser and remember them when you revisit. <a onClick={this.resetOptions}>Reset to default values.</a></span>
        </div>
        <div className="option">
          <label>
            <span title="Number of whitespace characters used to indent">Indentation<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
              <span style={{marginRight: "10px"}}><b>{this.state.indentLength}</b></span>
              <input className="slider is-fullwidth" onChange={this.handleChange} type="range"  step="1" min="1" max="10" value={this.state.indentLength} name="indentLength" ></input>
            </div>
          </label>
        </div>
        <div className="option">
          <label>
            <span title="Number of characters allowed in one line. The formatter will try to break statements to fit into the line width. Some statements cannot be broken in a good way (like a very long string literal) and therefore the line width can be exceeded in some cases.">Line width<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
              <span style={{marginRight: "10px"}}><b>{this.state.lineWidth}</b></span>
              <input className="slider is-fullwidth" onChange={this.handleChange} type="range" step="1" min="0" max="300" value={this.state.lineWidth} name="lineWidth" ></input>
            </div>
          </label>
        </div>
        <div className="option">
          <label>
            <span title="Number of different bracket classes. Brackets are given classes bracket-{number} for you to style matching brackets specifically. All kinds of brackets '{', '(' and '[' are treated equally.">Number of brackets<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
                <span style={{marginRight: "10px"}}><b>{this.state.numBrackets}</b></span>
                <input className="slider is-fullwidth" onChange={this.handleChange} type="range"  step="1" min="1" max="10" value={this.state.numBrackets} name="numBrackets" ></input>
            </div>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="includeComments" onChange={this.handleChange} checked={this.state.includeComments} type="checkbox"></input>
            <span title="Include comments in the format result"> Include Comments<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="alignExpressionsByEqual" onChange={this.handleChange} checked={this.state.alignExpressionsByEqual} type="checkbox"></input>
            <span title="Number of characters allowed in one line. The formatter will try to break statements to fit into the line width. Some statements cannot be broken in a good way (like a very long string literal) and therefore the line width can be exceeded in some cases."> Align paired expressions by equal<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="inlineCssToggle" onChange={this.handleChange} checked={this.state.inlineCssToggle} type="checkbox"></input>
            <span title="Inline the applied css attributes as style attributes on the html elements. If disabled, no inlining will be performed and only the class tags will be applied to the html elements.">InlineCss<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className={`option ${this.state.inlineCssToggle == false ? "no-display" : ""}`}>
          <label>
          <input className="checkbox" name="customCss" onChange={this.handleChange} checked={this.state.customCss} type="checkbox"></input>
          <span title="Use custom css">Use custom css<FaQuestionCircle></FaQuestionCircle></span>
          <textarea cols={100} rows={25} name="inlineCss" title="Check 'Use custom css' to edit" disabled={this.state.customCss == false} onChange={this.handleChange} className={`textarea ${this.state.inlineCssToggle == false ? "no-display" : ""}`} value={this.state.inlineCss} placeholder="inline css"></textarea>
          </label>
        </div>
      </div>
    )

  }
}