import React, { ChangeEvent } from "react";
import "./Formatter.sass";
import { CodeBlock } from './CodeBlock';
import { ErrorDisplay } from "./ErrorDisplay";
import { FormatterOptions } from "./FormatterOptions";
import { setConfig, format, FormatError } from "m-formatter";
import { AppInsightsService } from '../misc/appInsightsService';
import { lightThemeCss, darkThemeCss } from '../options';
import { AggregateError } from '../interfaces';
import { DbService } from '../misc/dbService';

interface IState
{
  code: string;
  result: string;
  errors: FormatError[];
  errorMessage: string;
  toggleOptions: boolean;
  formatting: boolean;
  formatErr: string;
  theme: "light"|"dark";
}

export class Formatter extends React.Component<{}, IState>
{
  codeRef = React.createRef() as React.RefObject<HTMLTextAreaElement>;
  optionComponent: FormatterOptions;
  codeblockRef = React.createRef() as React.RefObject<CodeBlock>;
  controlContainerRef = React.createRef() as React.RefObject<HTMLDivElement>;
  
  constructor(props)
  {
    super(props);
    this.state = {
      code: null,
      result: null,
      errors: null,
      errorMessage: null,
      toggleOptions: false,
      formatting: false,
      formatErr: null,
      theme: "light"
    }
  }

  async componentDidMount()
  {
    this.codeRef.current.addEventListener('keydown', (e) => {
      if (e.which == 9) 
      {
        let start = this.codeRef.current.selectionStart;
        let val = this.codeRef.current.value;
        val = val.substring(0, start) + " ".repeat(2) + val.substring(start);
        this.codeRef.current.value = val;
        this.codeRef.current.selectionStart = start + 2;
        this.codeRef.current.selectionEnd = start + 2;
        e.preventDefault();
      }
    });
    let state = await DbService.get<IState>("formatter");
    if(state)
    {
      this.codeRef.current.textContent = state.code;
      this.setState(state);
    }
    window.addEventListener("beforeunload", this.onBeforeUnload)
  }
  
  async storeState()
  {
    await DbService.store("formatter", {
      code: this.state.code,
      theme: this.state.theme
    })
  }

  onBeforeUnload = async () => {
    await this.storeState();
  }
  
  async componentWillUnmount()
  {
    await this.storeState();
  }

  format = async () => {
    if(this.state.code == null || this.state.code == "")
    {
      this.setState({
        formatErr: "Please enter some code to format"
      });
      return;
    }
    this.setState({
      formatting: true,
      formatErr: null,
      errors: null,
      errorMessage: null
    });
    try
    {
      try {
        let config = this.optionComponent.getConfig();
        if(this.optionComponent.state.customCss == false && config.inlineCssToggle == true)
          config.inlineCss = this.state.theme == "light" ? lightThemeCss : darkThemeCss;
        setConfig(config);
      } 
      catch (error) 
      {
        this.setState({
          errorMessage: "Invalid options passed. Please check if you have any invalid values configured in the options tab",
          toggleOptions: true
        })  
        return;
      }
      try
      {
        AppInsightsService.trackFormat();
        let [result, errors] = await format(this.state.code);
        
        if(errors != null && errors.length == 0)
          throw new AggregateError(errors);
          
        this.setState({result: result, errors});
        this.controlContainerRef.current.scrollIntoView();
      }
      catch(error)
      {
        console.log(error);
        if(error instanceof AggregateError)
        {
          this.setState({errors: error.errors})
          AppInsightsService.trackFormatError(error);
        }
        else if(error instanceof FormatError)
        {
          this.setState({errors: [error]})
          AppInsightsService.trackFormatError(error);
        }
        else
        {
          AppInsightsService.trackError("Unknown error during formatting", error);
        }
      }
    }
    finally
    {
      this.setState({
        formatting: false
      })
    }
  }

  toggleOptions = () => {
    this.setState({
      toggleOptions: !this.state.toggleOptions
    });
  }

  handleChange = (ev: ChangeEvent<any>) => {
    let d = {};
    if(ev.target.type == "checkbox")
      d[ev.target.name] = ev.target.checked;
    else
      d[ev.target.name] = ev.target.value;
    this.setState(d);
    
    if(ev.target.name == "theme" && !String.isNullOrEmpty(this.state.code) && !String.isNullOrEmpty(this.state.result))
    {
      setTimeout(async () => {
        await this.format();
      });
    }
  }

  back = (reset: boolean = false) => {
    let state: any = {
      result: null,
    }
    if(reset == true)
    {
      state.code = null;
      this.codeRef.current.value = null;
      this.codeRef.current.focus();
    }
    
    this.setState(state);
  }
  
  on_copyhtml_clicked = () => {
    this.codeblockRef.current.on_copyhtml_clicked();
  }
  
  on_copy_clicked = () => {
    this.codeblockRef.current.on_copy_clicked();
  }
  
  onPaste = (event) => {
    let paste = (event.clipboardData || (window as any).clipboardData).getData('text') as string;
    let lines = (paste.match(/\n/g) || []).length;
    lines = Math.min(Math.max(15, lines), 45);
    this.codeRef.current.style.height = lines+"em";
  }

  render()
  {
    return (
      <div className="formatter-container">
        <div className="error-container">
          {
            this.state.errors != null ? (
              <div>
                <div className="error">
                  Es sind Fehler aufgetreten
                </div>
                {
                  this.state.errors.map((e,i) => <div key={i}><ErrorDisplay error={e}></ErrorDisplay></div>) 
                }
                </div>
            ): ""
          }
        </div>
        <div>
          <span style={{marginRight: '10px'}}><b>Theme</b></span>
          <span className="control">
            <label className="radio">
              <input type="radio" name="theme" value="light" checked={this.state.theme == "light"} onChange={this.handleChange}/>
              Light
            </label>
            <label className="radio">
              <input type="radio" name="theme" value="dark" checked={this.state.theme == "dark"} onChange={this.handleChange}/>
              Dark
            </label>
          </span>
        </div>
        <div ref={this.controlContainerRef}>
          {
            this.state.errorMessage ? <div className="error">{this.state.errorMessage}</div> : ""
          }
          <div className="formatter-input-controls">
            <div>
              <button onClick={this.format} className={`button action-button ${this.state.formatting == true ? "is-loading" : ""}`} disabled={this.state.formatting == true}>Format</button>
            </div>
            {
              this.state.result != null ? (
                <>
                  <span>
                    <button className="button action-button" onClick={this.on_copy_clicked}>Copy Code</button>
                  </span>
                  <span>
                    <button className="button action-button" title="Copy HTML to your clipboard. You can paste this into word or other rich text editors to have colored code." onClick={this.on_copyhtml_clicked}>Copy HTML</button>
                  </span>
                  <span>
                    <button className="button action-button" onClick={() => this.back(false)}>Back</button>
                  </span>
                  <span>
                    <button className="button action-button" onClick={() => this.back(true)}>New Query</button>
                  </span>
                </>
              ) : ""
            }
          </div>
        </div>
        <div className={`formatter-input ${this.state.result != null ? "no-display" : ""}`}>
          <textarea ref={this.codeRef} className="textarea" cols={100} rows={10} name="code" onChange={this.handleChange} onPaste={this.onPaste} placeholder="Place your Power Query Code here"></textarea>
        </div>
        <div className="formatter-errors">
        {
          this.state.formatErr != null ? <span className="error">{this.state.formatErr}</span> : ""
        }
        </div>
        {
          <div className={`formatter-result ${this.state.result == null ? "no-display" : ""}`}>
            <CodeBlock ref={this.codeblockRef} formatted={this.state.result} backCallback={this.back} theme={this.state.theme}></CodeBlock>
          </div>
        }
        <div>
          {
            this.state.errorMessage ? <div className="error">{this.state.errorMessage}</div> : ""
          }
          <div className="formatter-input-controls">
            <div>
              <button onClick={this.format} className={`button action-button ${this.state.formatting == true ? "is-loading" : ""}`} disabled={this.state.formatting == true}>Format</button>
            </div>
            {
              this.state.result != null ? (
                <>
                  <span>
                    <button className="button action-button" onClick={this.on_copy_clicked}>Copy Code</button>
                  </span>
                  <span>
                    <button className="button action-button" title="Copy HTML to your clipboard. You can paste this into word or other rich text editors to have colored code." onClick={this.on_copyhtml_clicked}>Copy HTML</button>
                  </span>
                  <span>
                    <button className="button action-button" onClick={() => this.back(false)}>Back</button>
                  </span>
                  <span>
                    <button className="button action-button" onClick={() => this.back(true)}>New Query</button>
                  </span>
                </>
              ) : ""
            }
          </div>
          <div>
            <button onClick={this.toggleOptions} className={`button action-button ${this.state.toggleOptions == true ? "is-active": ""}`}>Options</button>
          </div>
          <div className={`${this.state.toggleOptions == false ? "no-display" : ""}`}>
            <FormatterOptions initializer={(c) => this.optionComponent = c}></FormatterOptions>
          </div>
        </div>
      </div>
    )
  }
}